body {
  font-family: "Sora", system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

$weights: (
  regular: 400,
  medium: 500,
  semibold: 600,
  bold: 700,
);

@function weight($weight-name) {
  @return map-get($weights, $weight-name);
}
